import React, { Component } from 'react';

class BetColumn extends Component {
    render() {
      return (
        <div
          id="Bet_Column"
          className="xuenn app-column xxs-12 xs-12 s-8 m-8 lg-8 xlg-8 xxlg-8 fixed-nopad height100"
        >
          <div id="Bet_Slip_Container" className="xuenn height100 relative-this">
            <div id="Bet_Panel">
              <div id="Bet_Panel_Header">
                <table className="bet_panel_header">
                  <tbody>
                    <tr>
                      <td
                        className="bet_panel_minimize event_trigger"
                        data-trigger="panel-toggle"
                      >
                        <i className="fa fa-angle-down" aria-hidden="true" />
                      </td>
                      <td
                        className="bet_panel_title event_trigger"
                        data-trigger="panel-toggle"
                      >
                        <p id="Bet_Title">BET SLIP</p>
                        <p id="Bet_MyMoney">1,340.00</p>
                      </td>
                      <td
                        className="bet_panel_add_selection event_trigger"
                        data-trigger="panel-toggle"
                      >
                        <span id="Badge_Count" className="badge-ctr bottom">
                          0
                        </span>
                        <div className="c_con">
                          <span className="_circle1" />
                          <span className="_circle1" />
                        </div>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
              <div id="Bet_Panel_Body">
                <div className="confirmed_drop" />
                <div className="bet_empty_notice">
                  <p>
                    <img src="img/empty-state.svg" alt="" />
                  </p>
                  <p>Your bet slip is currently empty.</p>
                </div>
                <div id="Bet_Settings">
                  <div className="bet_settings_header">
                    <p>BET SLIP SETTING</p>
                  </div>
                  <table className="bet_settings_popup">
                    <tbody>
                      <tr>
                        <td className="bet_odds_settings">
                          <p>Accept Better Odds</p>
                        </td>
                        <td className="bet_odds_toggle">
                          <i className="fa fa-toggle-on" aria-hidden="true" />
                        </td>
                      </tr>
                    </tbody>
                  </table>
                </div>
                <div className="back_drop" />
                <div id="Bet_Tickets_Container">
                  <div id="Bet_Tickets_Scrollable" className="event_trigger">
                    <div className="bet_empty">
                      <p>No Selection in the Bet Slip</p>
                    </div>
                  </div>
                  <div id="Bet_Input">
                    <div className="confirmed_drop2" />
                    <table className="bet_input_details">
                      <tbody>
                        <tr>
                          <td className="bet_input _right">
                            <span
                              className="clear_input event_trigger"
                              data-trigger="clear-input"
                            >
                              &#x2715;
                            </span>
                            <input
                              type="number"
                              tabIndex="-1"
                              min="0"
                              inputMode="numeric"
                              pattern="[0-9]*"
                              id="Bet_Stake_Input"
                              className="event_exp event_trigger"
                              readOnly
                              data-trigger="input-activated"
                              placeholder="Enter Stake"
                            />
                          </td>
                          <td className="bet_minmax">
                            <p>
                              Min Bet:
                              <span className="min_bet">5.00</span>
                            </p>
                            <p>
                              Max Bet:
                              <span className="max_bet">5,520.00</span>
                            </p>
                          </td>
                        </tr>
                      </tbody>
                    </table>
                  </div>
                  <div id="Bet_Notice_Summary_Container">
                    <div id="Bet_Summary" className="hide">
                      <table className="bet_summary">
                        <tbody>
                          <tr>
                            <td className="bet_summary_info">
                              <p>Estimated winnings:</p>
                            </td>
                            <td className="bet_summary_estimates">
                              <p>
                                <span className="styleColor">10,000.00</span>
                                <span className="s_orange" />
                              </p>
                            </td>
                          </tr>
                        </tbody>
                      </table>
                    </div>
                    <div id="Bet_Notice" className="">
                      <table className="bet_min_notice">
                        <tbody>
                          <tr>
                            <td className="bet_notice_icon">
                              <i
                                className="fa fa-exclamation-circle"
                                aria-hidden="true"
                              />
                            </td>
                            <td className="bet_notice_info">
                              You need to make a minimum of 3 bets to continue.
                            </td>
                          </tr>
                        </tbody>
                      </table>
                    </div>
                    <div id="Bet_Confirmed" className="hide">
                      <table className="bet_min_notice">
                        <tbody>
                          <tr>
                            <td className="bet_notice_icon">
                              <i
                                className="fa fa-info-circle"
                                aria-hidden="true"
                              />
                            </td>
                            <td className="bet_notice_info">
                              Your bet has been confirmed.
                            </td>
                          </tr>
                        </tbody>
                      </table>
                    </div>
                    <div id="Bet_Confirming">
                      <table className="bet_min_notice">
                        <tbody>
                          <tr>
                            <td className="bet_notice_icon">
                              <i
                                className="fa fa-info-circle"
                                aria-hidden="true"
                              />
                            </td>
                            <td className="bet_notice_info">
                              Confirming. Please wait.
                            </td>
                          </tr>
                        </tbody>
                      </table>
                    </div>
                  </div>
                </div>
                <div id="Bet_Function_Container">
                  <div id="Bet_Keyboard">
                    <div id="Bet_All_Keys">
                      <table className="bet_keys_section key_portrait">
                        <tbody>
                          <tr>
                            <td>
                              <div className="bet_key ind_key" data-value="1">
                                1
                              </div>
                            </td>
                            <td>
                              <div className="bet_key ind_key" data-value="2">
                                2
                              </div>
                            </td>
                            <td>
                              <div className="bet_key ind_key" data-value="3">
                                3
                              </div>
                            </td>
                            <td>
                              <div
                                className="bet_key ind_key quick_key"
                                data-value="200000"
                              >
                                + 200000
                              </div>
                            </td>
                          </tr>
                          <tr>
                            <td>
                              <div className="bet_key ind_key" data-value="4">
                                4
                              </div>
                            </td>
                            <td>
                              <div className="bet_key ind_key" data-value="5">
                                5
                              </div>
                            </td>
                            <td>
                              <div className="bet_key ind_key" data-value="6">
                                6
                              </div>
                            </td>
                            <td>
                              <div
                                className="bet_key ind_key quick_key"
                                data-value="400000"
                              >
                                + 400000
                              </div>
                            </td>
                          </tr>
                          <tr>
                            <td>
                              <div className="bet_key ind_key" data-value="7">
                                7
                              </div>
                            </td>
                            <td>
                              <div className="bet_key ind_key" data-value="8">
                                8
                              </div>
                            </td>
                            <td>
                              <div className="bet_key ind_key" data-value="9">
                                9
                              </div>
                            </td>
                            <td>
                              <div
                                className="bet_key ind_key quick_key"
                                data-value="1000000"
                              >
                                + 1000000
                              </div>
                            </td>
                          </tr>
                          <tr>
                            <td>
                              <div className="bet_key ind_key">
                                <span className="styleOpacity">.</span>
                              </div>
                            </td>
                            <td>
                              <div className="bet_key ind_key" data-value="0">
                                0
                              </div>
                            </td>
                            <td>
                              <div
                                className="bet_key ind_key styleFontSize"
                                data-value="del"
                              >
                                <i
                                  className="fa fa-window-close"
                                  aria-hidden="true"
                                />
                              </div>
                            </td>
                            <td>
                              <div
                                className="bet_key ind_key quick_key"
                                data-value="2000000"
                              >
                                + 2000000
                              </div>
                            </td>
                          </tr>
                        </tbody>
                      </table>
                      <table className="bet_keys_section key_landscape">
                        <tbody>
                          <tr>
                            <td>
                              <div className="bet_key ind_key" data-value="1">
                                1
                              </div>
                            </td>
                            <td>
                              <div className="bet_key ind_key" data-value="2">
                                2
                              </div>
                            </td>
                            <td>
                              <div className="bet_key ind_key" data-value="3">
                                3
                              </div>
                            </td>
                            <td>
                              <div className="bet_key ind_key" data-value="4">
                                4
                              </div>
                            </td>
                            <td>
                              <div className="bet_key ind_key" data-value="5">
                                5
                              </div>
                            </td>
                            <td>
                              <div className="bet_key ind_key" data-value="6">
                                6
                              </div>
                            </td>
                            <td>
                              <div
                                className="bet_key ind_key quick_key"
                                data-value="200000"
                              >
                                + 200000
                              </div>
                            </td>
                            <td>
                              <div
                                className="bet_key ind_key quick_key"
                                data-value="400000"
                              >
                                + 400000
                              </div>
                            </td>
                          </tr>
                          <tr />
                          <tr>
                            <td>
                              <div className="bet_key ind_key" data-value="7">
                                7
                              </div>
                            </td>
                            <td>
                              <div className="bet_key ind_key" data-value="8">
                                8
                              </div>
                            </td>
                            <td>
                              <div className="bet_key ind_key" data-value="9">
                                9
                              </div>
                            </td>
                            <td>
                              <div className="bet_key ind_key" data-value="0">
                                0
                              </div>
                            </td>
                            <td colSpan="2">
                              <div
                                className="bet_key ind_key styleFontSize"
                                data-value="del"
                              >
                                <i
                                  className="fa fa-window-close"
                                  aria-hidden="true"
                                />
                              </div>
                            </td>
                            <td>
                              <div
                                className="bet_key ind_key quick_key"
                                data-value="1000000"
                              >
                                + 1000000
                              </div>
                            </td>
                            <td>
                              <div
                                className="bet_key ind_key quick_key"
                                data-value="2000000"
                              >
                                + 2000000
                              </div>
                            </td>
                          </tr>
                        </tbody>
                      </table>
                    </div>
                  </div>
                </div>
              </div>
  
              <div id="Bet_Panel_Buttons" className="empty_b">
                <div className="sec_btn bet_buttons_empty">
                  <a
                    className="bet_settings width_50 flx event_trigger"
                    data-trigger="settings"
                  >
                    <i className="fa fa-cog" aria-hidden="true" />
                  </a>
                  <div
                    className="bet_ok_default width_50 flx event_trigger"
                    data-trigger="panel-toggle"
                  >
                    OK
                  </div>
                </div>
  
                <div className="sec_btn bet_buttons_default">
                  <a
                    className="bet_remove_all width_35 flx event_trigger"
                    data-trigger="remove-all"
                  >
                    REMOVE ALL
                  </a>
  
                  <a
                    className="bet_settings width_15 flx event_trigger"
                    data-trigger="settings"
                  >
                    <i className="fa fa-cog" aria-hidden="true" />
                  </a>
  
                  <a
                    className="bet_placebet width_50 flx event_trigger"
                    data-trigger="place-bet"
                  >
                    <div className="placebet_loader">
                      <i className="fa fa-circle-o-notch fa-spin fa-3x fa-fw" />
                      <span className="sr-only">Loading...</span>
                    </div>
                    <div>
                      <p>
                        <span className="bet_stake">0.00</span>
                      </p>
                      <p>PLACE BET</p>
                    </div>
                  </a>
                </div>
  
                <div className="sec_btn bet_buttons_default_notice">
                  <a
                    className="bet_remove_all width_35 flx event_trigger"
                    data-trigger="remove-all"
                  >
                    REMOVE ALL
                  </a>
  
                  <a
                    className="bet_settings width_15 flx event_trigger"
                    data-trigger="settings"
                  >
                    <i className="fa fa-cog" aria-hidden="true" />
                  </a>
  
                  <a
                    className="bet_ok_default width_50 flx event_trigger"
                    data-trigger="panel-toggle"
                  >
                    OK
                  </a>
                </div>
  
                <div className="sec_btn bet_buttons_extended_keys">
                  <a
                    className="bet_done width_100 flx event_trigger"
                    data-trigger="done-input"
                  >
                    DONE
                  </a>
                </div>
  
                <div className="sec_btn bet_buttons_confirming">
                  <a
                    className="bet_cancel_confirm width_50 flx event_trigger"
                    data-trigger="cancel-confirm"
                  >
                    CANCEL
                  </a>
                  <a
                    className="bet_confirmbet width_50 flx event_trigger"
                    data-trigger="confirm-bet"
                  >
                    <div className="confirm_loader">
                      <i className="fa fa-circle-o-notch fa-spin fa-3x fa-fw" />
                      <span className="sr-only">Loading...</span>
                    </div>
                    <div>
                      <p>
                        <span className="bet_stake">0.00</span>
                      </p>
                      <p>CONFIRM BET?</p>
                    </div>
                  </a>
                </div>
  
                <div className="sec_btn bet_buttons_confirmed">
                  <a
                    className="bet_ok_confirmed flx event_trigger"
                    data-trigger="panel-close"
                  >
                    OK
                  </a>
                </div>
  
                <div className="sec_btn bet_buttons_settings">
                  <a
                    className="bet_cancel_settings flx event_trigger"
                    data-trigger="cancel-settings"
                  >
                    CANCEL
                  </a>
                  <a
                    className="bet_save_settings flx event_trigger"
                    data-trigger="save-settings"
                  >
                    SAVE
                  </a>
                </div>
              </div>
            </div>
          </div>
        </div>
      );
    }
  }

  export default BetColumn;


