import React, { Component } from 'react';
import {
  SvgStringfootball,
  SvgStringAmFootballandBasketball,
  SvgStringTennis,
  SvgStringVolleyball,
  SvgStringBadminton,
} from '../SvgIcon';

class Sportlist extends Component{
  
    render(){
        function getTagName(tagName){
        switch(tagName){
            case "SvgStringfootball":
                return (<SvgStringfootball />);
            case "SvgStringAmFootballandBasketball":
                return (<SvgStringAmFootballandBasketball />);
            case "SvgStringTennis":
                return (<SvgStringTennis />);
            case "SvgStringVolleyball":
                return (<SvgStringVolleyball />);
            case "SvgStringBadminton":
                return (<SvgStringBadminton />);
            default:
                return ;
        }
      }
      return(
          <div className="sportlist">
            <span className={"btn-flat waves-effect with24heighticon icon baseColor-text text-light-1 "+ (this.props.isActive?"actived":"")}>
              {getTagName(this.props.tagName)}
            </span>
          </div>
      );
    }
  }

  class SideNav extends Component {
    render() {
      return (
        <div className="sidenav pColor base z-depth-5">
          <Sportlist tagName="SvgStringfootball" isActive={false} />
          <Sportlist tagName="SvgStringAmFootballandBasketball" isActive={true} />
          <Sportlist tagName="SvgStringTennis" isActive={false} />
          <Sportlist tagName="SvgStringVolleyball" isActive={false} />
          <Sportlist tagName="SvgStringBadminton" isActive={false} />
        </div>
      );
    }
  }

  export default SideNav;


