import React, { Component } from "react";
import PropTypes from "prop-types";

import TopFilter from "./TopFilter";
import BottomFilter from "./BottomFilter";
import SideNav from "./SideNav";
import ScrollNav from "./ScrollNav";
import TopNav from "./TopNav";
import BetColumn from "./BetColumn";
import BackToTop from "./BackToTop";
import Footer from "./Footer";

import BetSlip from "../BetSlip/BetSlip";
import CompetitionMainPanel from "../Marketline/CompetitionMainPanel";

class MainContent extends Component {
  constructor(props) {
    super(props);

    this.state=this.generateState(props);
  }
  
  generateState(props) {
    return {
      mainOddsDisplayType : props.mainOddsDisplayType,
      datas : props.datas
    }
  }

  componentWillReceiveProps(newProps) {
    let newState = this.generateState(newProps);
    this.setState(newState);
  }

  render() {

    function generateCompetitionMainPanel(datas) {
      return datas.map((element, i) => {
        return (
          <CompetitionMainPanel
            key={"competition-" + i}
            name ={element.competitionName}
            events = {element.events}
            isCollapse={element.isCollapse}
          />
        );
      });
    }

    return (
      <div
        className={
          "maincontent col s12 m12 l8 xl9 xxl8 xxxl8 " +
          this.state.mainOddsDisplayType
        }
      >
        <div className="scrollcontainer detectscroll">
          <BackToTop />
          <SideNav />
          <ScrollNav />
          <TopNav />
          <BetColumn />
          <div className="highresscoper">
            <TopFilter />
            <BottomFilter />
            <div className="divider" />
            {generateCompetitionMainPanel(this.state.datas)}
          </div>
          <BetSlip />
          <Footer />
        </div>
      </div>
    );
  }
}

MainContent.propTypes = {
  mainOddsDisplayType : PropTypes.string.isRequired,
  datas : PropTypes.array.isRequired
};

export default MainContent;


