import React, {Component} from 'react';
import {SvgStringPrevious, SvgStringArrowNext, SvgStringCancel} from '../SvgIcon.js';
import EventsContainer from '../Marketline/EventsContainer'

class TabItem extends Component {
    constructor(props) {
        super(props);
        this.changeFocusTabItem = this
            .changeFocusTabItem
            .bind(this);

        this.state = props.isSelect != null
            ? {
                isSelect: props.isSelect
            }
            : {
                isSelect: false
            };
    }

    componentWillReceiveProps(nextProps) {
        this.setState({isSelect: nextProps.isSelect});
    }

    changeFocusTabItem(e) {
        e.preventDefault();
        this
            .props
            .callTogglePanel();
    }

    render() {

        return (
            <span
                className={"btn-flat btn-tab waves-effect baseColor-text text-light-8 " + (this.state.isSelect
                ? "selected"
                : "")}
                data-tabtarget="tab-1"
                onClick={this.changeFocusTabItem}>
                <span className="tb">
                    <span className="tb-cell">
                        <span>{this.props.displayName}</span>
                    </span>
                </span>
            </span>
        );
    }
}

class OptPanel extends Component {
    constructor(props) {
        super(props);
        this.togglePanel = this
            .togglePanel
            .bind(this);

        const isOpen = props.isOpen != null
            ? props.isOpen
            : false;

        this.state = {
            isOpen: isOpen,
            focusTabIndex: 0,
            EventsContainerType: 'content_1'
        };
    }

    shouldComponentUpdate(nextProps, nextState) {
        return this.state !== nextState;
    }

    togglePanel(isOpen, selectedIndex) {
        // e.preventDefault();
        let currentState = this.state;
        currentState.isOpen = isOpen;
        if (isOpen) {
            currentState.focusTabIndex = selectedIndex;
            switch(selectedIndex){
                case 0:
                case 2:
                case 4:
                    currentState.EventsContainerType='content_1';
                    break;
                case 1:
                case 5:
                    currentState.EventsContainerType='content_2';
                    break;
                default:
                    currentState.EventsContainerType='loading';
            }
        }
        this.setState(currentState);
    }

    render() {
        let closePanelClick = this
            .togglePanel
            .bind(this, false, 0);
        let switchPanelClick = this
            .togglePanel
            .bind(this, true);

        function generateTabItem(focusTabIndex) {

            const displayNames = [
                '+HDP & O/Us',
                'CORRECT SCORE',
                'CORNERS',
                '1X2 & O/U BTTS',
                'HT / FT',
                'TOTAL GOALS'
            ];
            return displayNames.map(function (name, i) {
                return <TabItem
                    key={i}
                    callTogglePanel={switchPanelClick.bind(this, i)}
                    isSelect={i === focusTabIndex}
                    displayName={name}/>;
            })
        }

        return (
            <div
                className={"expansion-carousel z-depth-3 cards embed swipeable " + (this.state.isOpen
                ? ""
                : "collapsed")}>
                <div className="carousel-nav baseColor light-1 tab-t3 z-depth-3">
                    <div className="carousel">
                        <div className="previous">
                            <a className="btn-flat waves-effect baseColor-text text-light-8  ">
                                <span className="icon-previous">
                                    <SvgStringPrevious/>
                                </span>
                            </a>
                        </div>
                        <div className="next">
                            <a className="btn-flat waves-effect baseColor-text text-light-8 ">
                                <span className="icon-next">
                                    <SvgStringArrowNext/>
                                </span>
                            </a>
                        </div>
                        <div className="cancel" onClick={closePanelClick}>
                            <span
                                className="icon-cancel btn-flat waves-effect baseColor-text text-light-8 ">
                                <SvgStringCancel/>
                            </span>
                        </div>
                        <div className="carousel-scroll styleOverflow">
                            <div className="c-inline">{generateTabItem(this.state.focusTabIndex)}</div>
                        </div>
                    </div>
                </div>
                <EventsContainer type={this.state.EventsContainerType}/>
            </div>
        );
    }
}

export default OptPanel;


