import React, { Component } from "react";
import {SvgStringFav } from "../SvgIcon.js";
import MainMarketline from "../Marketline/MainMaketline";
import OptPanel from "../Marketline/OptPanel";

class CompetitionMainPanel extends Component {
  constructor(props) {
    super(props);

    this.togglePanel = this.togglePanel.bind(this);

    this.state = this.generateState(props,this.props.isCollapse);
  }

  generateState(props,currentCollapseStatus) {
    return {
          competitionName: props.name,
          events: props.events,
          isCollapse: currentCollapseStatus != null ? currentCollapseStatus : true
        };
  }

  togglePanel(e) {
    e.preventDefault();

    let currentState = this.state;
    currentState.isCollapse = !currentState.isCollapse;
    this.setState(currentState);
  }

  componentWillReceiveProps(newProps) {
    let newState = this.generateState(newProps,this.state.isCollapse);
    this.setState(newState);
  }

  render() {
    let cssClass = "collapsible-eventset ";
    cssClass += this.state.isCollapse ? "hideElement" : "";

    function generateEvenSet(key, event) {
      var evenSetCssClass = "eventset mainmarket";
      evenSetCssClass += event.isFirst ? "first" : "";

      return (
        <div key={key} className={evenSetCssClass}>
          <MainMarketline data={event.maketlineDatas} />{" "}
          <div className="divider" />
          <OptPanel isOpen={event.isFirst} />{" "}
        </div>
      );
    }

    function generateEvenSets(events) {
      return events.map((event, i) => generateEvenSet(i, event));
    }

    function generateCollapsibleEventSet(cssClass, events) {
      return <div className={cssClass}> {generateEvenSets(events)} </div>;
    }

    return (
      <div className="competition-main-panel">
        <div
          className="ex-title-t1 expandable baseColor light-1 country z-depth-3"
          onClick={this.togglePanel}
        >
          <div className="cflag de" />
          <span className="baseColor-text text-light-8">
            <span className="tb">
              <span className="tb-cell">
                <span className="twoline"> {this.state.competitionName} </span>{" "}
              </span>{" "}
            </span>{" "}
          </span>{" "}
          <span className="btn-flat waves-effect baseColor-text text-light-5 fav center ">
            <span className="tb">
              <span className="tb-cell">
                <span className=" icon-fav ">
                  <SvgStringFav />
                </span>{" "}
              </span>{" "}
            </span>{" "}
          </span>{" "}
        </div>{" "}
        {generateCollapsibleEventSet(cssClass, this.state.events)}{" "}
      </div>
    );
  }
}

export default CompetitionMainPanel;



