import React, { Component } from "react";
import { SvgStringArrowDownBlack, SvgStringArrowUpBlack } from "../SvgIcon";

class BetSlip extends Component {
  render() {
    return (
      <div className="betslip out-area">
        <div className="leftpaneltest betslip-img true-area col s12 m12 l8 xl7">
          <div className="ex-title-t2 atColor light-1 z-depth-3">
            <span className="btn-flat waves-effect baseColor-text text-light-1 arrow center ">
              <span className="tb">
                <span className="tb-cell">
                  <span className=" icon-arrow ">
                      <SvgStringArrowUpBlack />
                      <SvgStringArrowDownBlack />
                  </span>
                </span>
              </span>
            </span>
            <span className="baseColor-text text-light-1">
              <span className="tb">
                <span className="tb-cell mini-toggle">
                  <span className="twoline">BET SLIP</span>
                  <span>
                    1, 340.00
                    <span className="atColor-text text-light-2 currency">
                      SGD
                    </span>
                    <span />
                  </span>
                </span>
              </span>
            </span>
          </div>
          <div className="bets-cont2">
            <div className="proto_betselection empty1">Empty betslip</div>
          </div>
          <button className="btn baseColor-text text-light-1 sColor lv-2 waves-effect waves-light">
            <span className="icontext">Place bet</span>
          </button>
        </div>
      </div>
    );
  }
}

export default BetSlip;



