import React, { Component } from 'react';
import {SvgStringSetting,SvgStringMenu } from '../SvgIcon';

class CarouselItem extends Component {
  render() {
    return (
      <a
      className={"btn-flat waves-effect baseColor-text text-light-1 "+ (this.props.isSelect?"selected":"")}
      href="#">
        {this.props.displayName}
      </a>
    );
  }  
}

class ScrollNav extends Component {
  render() {
    return (
      <div className="scroll-nav top-nav pColor base tier2 scroll-active">
        <div className="content">
          <div className="carousel">
            <span className="btn-flat waves-effect with20heighticon icon-menu baseColor-text text-light-1">
                <SvgStringMenu/>
            </span>
            <CarouselItem displayName="IN-PLAY" isSelect={true}/>
            <CarouselItem displayName="SOON" isSelect={false}/>
            <CarouselItem displayName="TODAY" isSelect={false}/>
            <CarouselItem displayName="EARLY" isSelect={false}/>
            <CarouselItem displayName="PARLAY" isSelect={false}/>
            <span className="rightpart">
              <span className="baseColor-text text-light-12 time">
                17 Mar 22:04:03
              </span>
              <span className="btn-flat waves-effect with24heighticon icon-back baseColor-text text-light-1">           
                  <SvgStringSetting/>
              </span>
            </span>
          </div>
        </div>
      </div>
    );
  }
}

  export default ScrollNav;


