import React, {Component} from 'react';
import BetNavigation from './BetNavigation';
import BetTemplate from './BetTemplate';
import MainContent from './MainContent/MainContent';
import RightPanelDesktop from './RightPanel/RightPanelDesktop';

function detectEventDataPath(){
  const url = '/data/';
  var query = window.location.search.substring(1);
  switch(query){
    case 'events=50':
      return url+'50/';
    case 'events=100':
      return url+'100/';
    case 'events=300':
      return url+'300/';
    default:
      return url+'200/';
  }
}

class App extends Component {
  constructor(props){
    super(props);
    this.timer = this.timer.bind(this);

    this.state = {
      error: null,
      isLoaded: false
    }
  }

  componentDidMount() {
    let intervalId = setInterval(this.timer, 1000);
    let newState = this.state;
    newState.intervalId = intervalId;
    this.setState(newState);
  }

  componentWillUnmount(){
    clearInterval(this.state.intervalId);
  }

  timer() {


    const fileNames = ['data_1.json','data_2.json','data_3.json','data_4.json','data_5.json'];

    let randonIndex = Math.floor(Math.random()*10)%5;
    fetch(`${detectEventDataPath()}${fileNames[randonIndex]}`)
    .then(res => res.json())
    .then(
      result => {
        this.setState({
          isLoaded: true,
          datas: result
        });
      },
      error => {
        this.setState({
          isLoaded: true,
          error
        });
      }
    );
  }

  render() {
    const { error, isLoaded, datas } = this.state;

    const mainOddsDisplayType= {
      selected_1st:'selected_1st',
      selected_2nd:'selected_2nd',
      selected_3rd:'selected_3rd'
    };

    if (error) {
      return <div>Error: {error.message}</div>;
    } else if(!isLoaded){
      return <div>Loading...</div>;
    } else {
      return (
        <object className="devicedetect baseColor light-3 desktop allelements xuenn">
          <BetTemplate/>
          <BetNavigation/>
          <div className="row">
            <MainContent mainOddsDisplayType={mainOddsDisplayType.selected_1st} datas={datas}/>
            <RightPanelDesktop/>
          </div>
        </object>
      );
    }
  }
}

export default App;



