import React, {Component} from 'react';
import {SvgStringTv, SvgStringMybet} from '../SvgIcon';

class ButtonlistItem extends Component {
  render() {

    function getIcon(type) {
      switch (type) {
        case "tv":
          return (
            <span className="icon">
              <span className="icon-tv">
                <SvgStringTv/>
              </span>
            </span>
          );
        case "update":
          return (
            <span className="icon tbd">
              <span className="icon-soccer">
              </span>
            </span>
          );
        case "mybet":
          return (
            <span className="icon">
              <span className="icon-mybet">
                <SvgStringMybet/>
              </span>
            </span>
          );
        default:
          return;
      }
    };

    return (
      <span
        className="tb-cell buttonlist btn-flat waves-effect atColor-sub-text text-lv-5">
        {getIcon(this.props.type)}
        <span className="tb center text">
          <span className="tb-cell">
            <span>{this.props.displayName}</span>
          </span>
        </span>
      </span>
    );
  }
}

class RightPanelDesktop extends Component {
  render() {
    let dummyElements =[];
    for(let i=0;i<=5;i++){
      dummyElements.push(
        <div key={i} className="dummyelement">Element</div>
      );
    }

    return (
      <div className="col l4 xl3 xxl4 xxxl4 m0 s0 rightpaneldesktop ">
        <div className="col l4 xl3 xxl4 xxxl4 rightpanelcontainer z-depth-5">
          <div className="bottom-nav baseColor light-1 ">
            <div className="tb">
              <ButtonlistItem displayName="Watch" type="tv"/>
              <ButtonlistItem displayName="Updates" type="update"/>
              <ButtonlistItem displayName="My bets" type="mybet"/>
            </div>
          </div>
          {dummyElements}
        </div>
      </div>
    );
  }
}

export default RightPanelDesktop;



