import React, { Component } from "react";
import { SvgStringTv, SvgStringFav } from "../SvgIcon.js";

class MainMarketline extends Component {
  constructor(props) {
    super();

    this.state = this.generateState(props);
  }
  
  generateState(props) {
    return {
      marketlineDatas: props.data
    }
  }

  componentWillReceiveProps(newProps) {
    let newState = this.generateState(newProps);
    this.setState(newState);
  }

  render() {
    function generateMaketlineName(data) {
      return (
        <tr key={"maketlineName-" + data.id}>
          <td className="teamname left-align colg-left">
            <div className="tb">
              <div className="tb-cell score">
                <span className="atColor-text text-light-3">{data.score}</span>
              </div>
              <div className="tb-cell">
                <div className="teamnemaecontianer">
                  <span className="wrapped">
                    <span className="redcard" />
                    {data.name}
                  </span>
                </div>
              </div>
            </div>
          </td>
        </tr>
      );
    }

    function generateSubtitleElement(
      id,
      cssClassName,
      displayName,
      colSpanNumber
    ) {
      var css = "subtitle " + cssClassName;

      switch (colSpanNumber) {
        case 0:
          return <th key={"subTitle-" + id} className={cssClassName} />;
        case 2:
          return (
            <th key={"subTitle-" + id} className={css} colSpan={colSpanNumber}>
              <span className="baseColor-text text-light-8">{displayName}</span>
            </th>
          );
        default:
          return (
            <th key={"subTitle-" + id} className={css}>
              <span className="baseColor-text text-light-8">{displayName}</span>
            </th>
          );
      }
    }

    function generateSubTitleElements(datas) {
      return (
        <tr>
          {datas.map(item =>
            generateSubtitleElement(
              item.id,
              item.betTypeTitle.cssClassName,
              item.betTypeTitle.displayName,
              item.betTypeTitle.colSpanNumber
            )
          )}
        </tr>
      );
    }

    function generateOddsElement(id, itemData, EvenOddsType) {
      if (typeof itemData === "undefined") {
        return;
      }
      switch (itemData.type) {
        case 0:
          return (
            <td key={EvenOddsType + id} className={itemData.cssClassName} />
          );
        case 2:
          return (
            <td key={EvenOddsType + id} className={itemData.cssClassName}>
              <div className="odds ">
                <div className="tb">
                  <div className="tb-cell">
                    <div className="center hdp">
                      <div className="tb">
                        <div className="tb-cell">{itemData.displayData[0]}</div>
                      </div>
                    </div>
                    <div className="center oddsvalue">
                      <div className="tb">
                        <div className="tb-cell">{itemData.displayData[1]}</div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </td>
          );
        case 1:
          return (
            <td key={EvenOddsType + id} className={itemData.cssClassName}>
              <div className="odds ">
                <div className="tb">
                  <div className="tb-cell">
                    <div className="center oddsvalue">
                      <div className="tb">
                        <div className="tb-cell">{itemData.displayData[0]}</div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </td>
          );
      }
    }

    function generateDrawOddsElements(datas) {
      return (
        <tr>
          {datas.map(item =>
            generateOddsElement(item.id, item.betTypeOdds[2], "Draw")
          )}
        </tr>
      );
    }

    function generateEvenOddsElements(datas) {
      return (
        <tr>
          {datas.map(item =>
            generateOddsElement(item.id, item.betTypeOdds[1], "Even")
          )}
        </tr>
      );
    }

    function generateOddsElements(datas) {
      return (
        <tr>
          {datas.map(item =>
            generateOddsElement(item.id, item.betTypeOdds[0], "Odd")
          )}
        </tr>
      );
    }

    return (
      <div className="baseColor light-1 z-depth-3 cards swipeable ">
        <div className="eventscontainer mainevent">
          <table className="events fixed">
            <colgroup>
              <col className="teamname" />
              <col className="oddscontent t1" />
            </colgroup>
            <tbody>
              <tr>
                <td className="childeventlabel left-align">
                  <span className=" baseColor-text text-light-1 baseColor light-7">
                    1H 27:41
                  </span>
                  <span className=" baseColor-text text-light-8 allmarkets">
                    134 &gt;
                  </span>
                  <span className="atColor-sub-text text-lv-3">N</span>
                </td>
                <td className="t1 xscrollable" rowSpan="4">
                  <div className="carousel ">
                    <div
                      className="content scorllquery right-align"
                      id="scorllquery"
                    >
                      <table className="contenttb fixed">
                        <colgroup>
                          <col className="colg-1st" />
                          <col className="colg-1st" />
                          <col className="colg-1st" />
                          <col className="colg-2nd" />
                          <col className="colg-2nd" />
                          <col className="colg-2nd" />
                          <col className="colg-3rd" />
                          <col className="colg-3rd" />
                          <col className="colg-3rd" />
                          <col className="colg-4th" />
                          <col className="colg-4th" />
                          <col className="colg-4th" />
                        </colgroup>
                        <tbody>
                          {generateSubTitleElements(
                            this.state.marketlineDatas.oddsDatas
                          )}
                          {generateOddsElements(
                            this.state.marketlineDatas.oddsDatas
                          )}
                          {generateEvenOddsElements(
                            this.state.marketlineDatas.oddsDatas
                          )}
                          {generateDrawOddsElements(
                            this.state.marketlineDatas.oddsDatas
                          )}
                        </tbody>
                      </table>
                    </div>
                  </div>
                </td>
              </tr>
              {generateMaketlineName(this.state.marketlineDatas.teamNames[0])}
              {generateMaketlineName(this.state.marketlineDatas.teamNames[1])}
              <tr>
                <td className="left-align colg-left">
                  <span className="btn-flat waves-effect atColor-sub-text text-lv-5 fav center ">
                    <span className="tb">
                      <span className="tb-cell">
                        <span className="icon-fav">
                          <SvgStringFav />
                        </span>
                      </span>
                    </span>
                  </span>
                  <span className="btn-flat waves-effect atColor-sub-text text-lv-5 center ">
                    <span className=" icon-nofitication icon ">
                      <img src="img/notification.png" alt="notification" />
                    </span>
                  </span>
                  <span className="btn-flat waves-effect atColor-sub-text text-lv-5 center ">
                    <img src="img/bg.png" alt="bg" />
                  </span>
                  <span className="btn-flat waves-effect atColor-sub-text text-lv-5 center tv">
                    <span className="icon baseColor-text text-light-2">
                      <span className="icon-tv">
                        <SvgStringTv />
                      </span>
                    </span>
                  </span>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    );
  }
}

export default MainMarketline;



