import React, { Component } from "react";

class BetTemplate extends Component {
  render() {
    return (
      <div className="bet_template bet_left event_trigger hide">
        <table className="bet_ticket_format">
          <tbody>
            <tr>
              <td className="bet_content">
                <p>Soccer Handicap</p>
                <p>08-08 UEFA SUPER CUP</p>
                <p>
                  Real Madrid
                  <span className="s_red">1</span>
                  v Manchester United
                </p>
                <p>
                  Real Madrid @
                  <span className="s_red">0.76</span>
                </p>
              </td>
              <td className="bet_delete">&#x2715;</td>
            </tr>
          </tbody>
        </table>
        <div className="confirmed_section">
          <p>
            Kashima Anthlers
            <span className="s_red">+1</span>
            @
            <span className="s_red">1.96</span>
          </p>
        </div>
      </div>
    );
  }
}

export default BetTemplate;



