import React, { Component } from 'react';
import { SvgStringArrowDownBlack } from '../SvgIcon';

class BottomFilter extends Component {
  render() {
    return (
      <div className="bottom-filter ex-title-t4 baseColor light-1 z-depth-3 withtwodropdown nocorp ">
        <div className="rightalign">
          <span className="baseColor-text text-light-6">
            <span className="tb fixedwidth">
              <span className="tb-cell">
                <span className="baseColor-text text-light-8 dropdown ">
                  <span className="tooglemarkettype expanded">
                    <span className="opt_1st" data-pageview="selected_1st">
                      1st Half
                    </span>
                    <span className="opt_2nd" data-pageview="selected_2nd">
                      Goals
                    </span>
                    <span className="opt_3rd" data-pageview="selected_3rd">
                      Team Goals O/U
                    </span>
                  </span>
                  <label
                    className="submarkettype baseColor-text text-light-1"
                    htmlFor="submarkettype"
                  >
                    <span className="icon-arrow ">
                        <SvgStringArrowDownBlack />
                    </span>
                    <select name="submarkettype" id="submarkettype">
                      <optgroup label="Main Markets">
                        <option value="selected_1st">1st Half</option>
                        <option value="selected_2nd">Goals</option>
                        <option value="selected_3rd">Team Goals O/U</option>
                      </optgroup>
                    </select>
                  </label>
                </span>
              </span>
            </span>
          </span>
        </div>
        <div>
          <span className="baseColor-text text-light-8">
            <span className="tb fixedwidth">
              <span className="tb-cell">
                <span className="baseColor-text text-light-8 dropdown collapsed twoline">
                  <label className="markettype" htmlFor="markettype">
                    <select name="markettype" id="markettype">
                      <option value="page-mainmarkets">Main Markets</option>
                      <option value="page-othermarkets">Correct Score</option>
                      <option value="page-othermarkets">
                        1X2 &amp; O/U BTTS
                      </option>
                      <option value="page-othermarkets">HT/FT</option>
                      <option value="page-othermarkets">Total Goals</option>
                      <option value="page-othermarkets">Double Chance</option>
                      <option value="page-othermarkets">Corners</option>
                      <option value="page-othermarkets">Outright</option>
                    </select>
                    <span className="icon-arrow">
                        <SvgStringArrowDownBlack />
                    </span>
                  </label>
                </span>
              </span>
            </span>
          </span>
        </div>
      </div>
    );
  }
}

export default BottomFilter;


