import React, {Component} from 'react';
import {SvgStringFilter} from '../SvgIcon';

class TopFilter extends Component {
  render() {
    return (
      <div
        className="top-filter ex-title-t3 pColor light-1 z-depth-3 withdropdown nocorp">
        <span
          className="btn-flat waves-effect baseColor-text text-light-1 filter center ">
          <span className="tb">
            <span className="tb-cell">
              <span className=" icon-filter ">
                <SvgStringFilter/>
              </span>
            </span>
          </span>
        </span>
        <span className="otherfilters">
          <span className="sortby">
            <span className="text baseColor-text text-light-1">Sort |</span>
            <span>icon</span>
          </span>
          <span className="competitions">
            <span className="text baseColor-text text-light-1">Compettitions |</span>
            <span className="text baseColor-text text-light-1">All</span>
          </span>
          <span className="showopt on">
            <span className="text baseColor-text text-light-1">Show extra markets |</span>

            <span className="togglebutton show-market waves-effect">
              <span className="oval">
                <span className="leftround"></span>
                <span className="rightround"></span>
              </span>
            </span>
          </span>
        </span>
        <div>
          <span className="baseColor-text text-light-1">
            <span className="tb fixedwidth">
              <span className="tb-cell">
                <span className="baseColor-text text-light-1 dropdown collapsed twoline sportname">
                  <span className="text ">Football</span>
                </span>
              </span>
            </span>
          </span>
        </div>
      </div>
    );
  }
}

export default TopFilter;


