import React, { Component } from 'react';
import {
  SvgStringfootball,
  SvgStringAmFootballandBasketball,
  SvgStringTennis,
  SvgStringVolleyball,
  SvgStringBadminton,
  SvgStringPrevious,
  SvgStringArrowNext
} from '../SvgIcon';

class NavItem extends Component {
  render() {
    function getIcon(type) {
      switch (type) {
        case "SvgStringfootball":
          return (<SvgStringfootball/>);
        case "SvgStringAmFootballandBasketball":
          return (<SvgStringAmFootballandBasketball/>);
        case "SvgStringTennis":
          return (<SvgStringTennis/>);
        case "SvgStringVolleyball":
          return (<SvgStringVolleyball/>);
        case "SvgStringBadminton":
          return (<SvgStringBadminton/>);
        default:
          break;
      }
    };

    return (
      <a
        className={"btn-flat waves-effect baseColor-text text-light-8 sports "+(this.props.isSelect?"selected":"")}
        href="#">
        <span className="sporttb center">
          <span className="tb-cell">
            <span className="icon with24heighticon hasdot">
              {getIcon(this.props.type)}
              <span className="badge">{this.props.badgeNumber}</span>
            </span>
          </span>
        </span>
        <span className="sporttb center text">
          <span className="tb-cell">
            <span>{this.props.sportTitle}</span>
          </span>
        </span>
      </a>
    );
  }
}
class TopNav extends Component {
    render() {
      return (
        <div className="top-nav baseColor light-1 tier3 z-depth-5 center sportscarousel hasbackbutton hasnextbutton ">
          <a className="btn-flat waves-effect baseColor-text text-light-8 hide previous ">
            <span className="icon-previous">
                <SvgStringPrevious/>
            </span>
          </a>
          <a className="btn-flat waves-effect baseColor-text text-light-8 hide next">
            <span className="icon-next">
                <SvgStringArrowNext/>
            </span>
          </a>
          <div className="content">
            <div className="carousel">
              <NavItem sportTitle="Soccer test test test" type="SvgStringfootball" badgeNumber={12} isSelect={true}/>
              <NavItem sportTitle="Basketball &amp; Rugby" type="SvgStringAmFootballandBasketball" badgeNumber={1} isSelect={false}/>
              <NavItem sportTitle="Tennis" type="SvgStringTennis" badgeNumber={2} isSelect={false}/>
              <NavItem sportTitle="Volleyball" type="SvgStringVolleyball" badgeNumber={3} isSelect={false}/>
              <NavItem sportTitle="Badminton" type="SvgStringBadminton" badgeNumber={4} isSelect={false}/>
              <NavItem sportTitle="Badminton test2" type="SvgStringBadminton" badgeNumber={5} isSelect={false}/> 
            </div>
          </div>
        </div>
      );
    }
  }

  export default TopNav;


