import React, {Component} from "react";
import {SvgStringfootball, SvgStringTv, SvgStringStatement, SvgStringMybet} from "./SvgIcon";

class BetNavigation extends Component {
  render() {
    return (
      <div
        id="Bet_Navigation"
        className="col-lg-6 col-md-offset-0 col-md-12 col-xs-offset-0 col-xs-12 col-xs-offset-0 col-sm-12">
        <table className="bet_navigation">
          <tbody>
            <tr>
              <td>
                <a className="bet_nav_btn">
                  <SvgStringfootball/>
                  <span>SPORTS</span>
                </a>
              </td>
              <td>
                <a className="bet_nav_btn">
                  <span className="icon">
                    <span className="icon-tv">
                      <SvgStringTv/>
                    </span>
                  </span>
                  <span>WATCH</span>
                </a>
              </td>
              <td className="event_trigger" data-trigger="panel-toggle">
                <a id="Bet_Slip_Btn" className="bet_nav_btn bet-selection">
                  <span id="Bet_Slip_Count">0</span>
                  <span>Bet Slip</span>

                  <div className="c_con">
                    <span className="_circle1"/>
                    <span className="_circle1"/>
                  </div>
                </a>
              </td>
              <td>
                <a className="bet_nav_btn">
                  <span className="icon">
                    <span className="icon-soccer">
                      <SvgStringStatement/>
                    </span>
                  </span>
                  <span>UPDATES</span>
                </a>
              </td>
              <td>
                <a className="bet_nav_btn">
                  <span className="icon">
                    <span className="icon-mybet">
                      <SvgStringMybet/>
                    </span>
                  </span>
                  <span>MY BETS</span>
                </a>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    );
  }
}

export default BetNavigation;



